import "core-js/modules/es.array.push.js";
import { useRouter } from 'vue-router';
import { _isMobile } from '@/utils/common';
import { computed } from 'vue';
import { useStore } from 'vuex';
import pcDefault from '@/components/layout/pcDefault.vue';
import mDefault from '@/components/layout/mDefault.vue';
import Loading from '@/components/Modal/loading.vue';
export default {
  components: {
    pcDefault,
    mDefault,
    Loading
  },
  setup() {
    const router = useRouter();
    const store = useStore();
    const goPage = () => {
      if (location.pathname !== '/') {
        router.push({
          path: location.pathname
        });
      } else {
        if (_isMobile()) {
          router.push({
            path: '/home'
          });
        } else {
          router.push({
            path: '/home'
          });
        }
      }
    };
    goPage();
    const layout = computed(() => {
      return router.currentRoute.value.meta.layout || 'limit';
    });
    const loadingShow = computed(() => store.state.loadingShow);
    return {
      layout,
      loadingShow
    };
  }
};