import { computed } from 'vue';
import HeaderNav from '@/components/pc/HeaderNav.vue';
import FooterCom from '@/components/pc/Footer.vue';
import { useI18n } from 'vue-i18n';
import { useStore } from 'vuex';
export default {
  components: {
    HeaderNav,
    FooterCom
  },
  setup() {
    const {
      locale
    } = useI18n();
    const store = useStore();
    const getLangFontClass = computed(() => {
      const className = locale.value === 'en-US' ? 'font_en' : 'font_zh';
      return className;
    });
    const walletListShow = computed(() => store.state.modal.walletModal);
    const disconnectShow = computed(() => store.state.modal.disconnectModal);
    return {
      getLangFontClass,
      walletListShow,
      disconnectShow
    };
  }
};