import { createI18n } from 'vue-i18n';
import zhTW from './zh-TW.js';
import enUS from './en-US.js';
import jaJP from './ja-JP.js';
// getLang
const getLang = () => {
  console.log('navigator.language', navigator.language);
  const langType = navigator.language || '';
  if (localStorage.getItem('langType')) {
    return localStorage.getItem('langType');
  } else if (
    langType === 'zh-TW' ||
    langType === 'en-US' ||
    langType === 'ja-JP'
  ) {
    return langType;
  }
  return 'en-US';
};

const i18n = createI18n({
  legacy: false,
  globalInjection: true,
  locale: getLang(),
  messages: {
    'zh-TW': zhTW,
    'en-US': enUS,
    'ja-JP': jaJP
  }
});

export default i18n;
