import { defineComponent, watch, ref } from 'vue';
export default defineComponent({
  name: 'Toast',
  props: {
    type: {
      type: String,
      default: ''
    },
    imgIconType: {
      type: String,
      default: ''
    },
    message: {
      type: String,
      default: ''
    }
  },
  // vue3
  setup(props) {
    let msg = ref('');
    watch(() => props.message, newVal => {
      msg.value = newVal;
    });
    return {
      msg
    };
  }
});