import { createRouter, createWebHashHistory } from 'vue-router'; //createWebHashHistory,createWebHistory
import { _isMobile } from '@/utils/common';
// import { h5Router } from './h5';
import { pcRouter } from './pc';
const commonRouter = [];
// pc、h5 router config
const routes = _isMobile()
  ? [...commonRouter, ...pcRouter]
  : [...commonRouter, ...pcRouter];
const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  // history: createWebHashHistory(),
  routes
});
router.afterEach(() => {
  // go back top
  window.scrollTo(0, 0);
});
export default router;
