import { createVNode, render } from 'vue';
import MyToast from './toast.vue';

export default {
  install: app => {
    let isToast = false;
    app.config.globalProperties.$myToast = function (opts) {
      if (!isToast) {
        isToast = true;
        let vm = createVNode(MyToast);
        let container = document.createElement('div');
        render(vm, container);
        document.body.appendChild(container);
        vm.component.props.message = opts.message || '';
        vm.component.props.type = opts.type || '';
        vm.component.props.imgIconType = opts.imgIconType || '';
        let duration = opts.duration || 3000;
        setTimeout(() => {
          document.body.removeChild(container);
          isToast = false;
          if (opts.onClose) {
            opts.onClose();
          }
        }, duration);
      }
    };
  }
};
